<template>
  <div class="nowPlayClass">
    <ul>
      <router-link tag="li" v-for="film in filmsList" :key="film.filmId" :to="'/detils/'+film.filmId">
        <div class="filmLogo">
          <img :src="film.poster" alt />
        </div>
        <div class="filmIntroduce">
          <div>
            <span
              style="font-size: 16px;height: 22px;line-height: 22px;margin-right: 5px;"
            >{{film.name}}</span>
            <span class="film-item">{{film.item.name}}</span>
          </div>
          <p>
            观众评分&nbsp;
            <i style="color: #ffb232;">{{film.grade}}</i>
          </p>
          <p
            style="white-space: nowrap;overflow: hidden;text-overflow:ellipsis;width:100%;"
          >主演：{{film.director}}</p>
          <p>{{film.nation}} | {{film.runtime}}分钟</p>
        </div>
        <div class="nowPlayingFilm-buy">购票</div>
      </router-link>
    </ul>
  </div>
</template>

<script>
import {mapState} from 'vuex'
import {instance} from "@/utils/http";
export default {
  data() {
    return {
      filmsList: []
    };
  },
  computed:{
    ...mapState('cityStore',['defaultCityId']),
  },
  created() {
    instance(`/gateway?cityId=${this.defaultCityId}&pageNum=1&pageSize=10&type=1&k=1928784`, {
      headers: {
        "X-Host": "mall.film-ticket.film.list"
      }
    })
      .then(res => {
        this.filmsList = res.data.data.films;
      })
      .catch(err => {
        console.log(err);
      });
  }
};
</script>
<style lang="scss">
.nowPlayClass {
  width: 100%;
  ul {
    margin-left: 15px;
    li {
      width: 100%;
      margin-top: 15px;
      height: 94px;
      position: relative;
      display: flex;
      .filmLogo {
        width: 66px;
        height: 94px;
        img {
          width: 100%;
        }
      }
      .filmIntroduce {
        width: calc(100% - 116px);
        padding: 0 10px;
        .film-item {
          font-size: 9px;
          color: #fff;
          background-color: #d2d6dc;
          height: 14px;
          line-height: 22px;
          padding: 0 2px;
          border-radius: 2px;
        }
      }
      .nowPlayingFilm-buy {
        line-height: 25px;
        height: 25px;
        width: 50px;
        color: #ff5f16;
        font-size: 13px;
        text-align: center;
        border-radius: 2px;
        position: relative;
        border: 1px solid #ff5f16;
        top: 50%;
        right: 4%;
        transform: translateY(-50%);
      }
    }
  }
}
</style>